/*----- spinner - 7 -----*/

.spinner-7 {
  width: 70px;
  text-align: center;
}

.spinner-7 > div {
  width: 18px;
  height: 18px;
  background-color: #333;

  border-radius: 100%;
  display: inline-block;
  -webkit-animation: spinner-7-bounce-delay 1.4s infinite ease-in-out both;
  animation: spinner-7-bounce-delay 1.4s infinite ease-in-out both;
}

.spinner-7 .bounce-7-1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.spinner-7 .bounce-7-2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

@-webkit-keyframes spinner-7-bounce-delay {
  0%, 80%, 100% {
    -webkit-transform: scale(0)
  }
  40% {
    -webkit-transform: scale(1.0)
  }
}

@keyframes spinner-7-bounce-delay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1.0);
    transform: scale(1.0);
  }
}
