/*----- spinner - 10 -----*/

.spinner-10 {
  width: 40px;
  height: 40px;
  position: relative;
}

.spinner-10 .sk-circle {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

.spinner-10 .sk-circle:before {
  content: '';
  display: block;
  margin: 0 auto;
  width: 15%;
  height: 15%;
  border-radius: 100%;
  -webkit-animation: spinner-10-circle-fade-delay 1.2s infinite ease-in-out both;
  animation: spinner-10-circle-fade-delay 1.2s infinite ease-in-out both;
}

.spinner-10 .sk-circle-10-2 {
  -webkit-transform: rotate(30deg);
  -ms-transform: rotate(30deg);
  transform: rotate(30deg);
}

.spinner-10 .sk-circle-10-3 {
  -webkit-transform: rotate(60deg);
  -ms-transform: rotate(60deg);
  transform: rotate(60deg);
}

.spinner-10 .sk-circle-10-4 {
  -webkit-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  transform: rotate(90deg);
}

.spinner-10 .sk-circle-10-5 {
  -webkit-transform: rotate(120deg);
  -ms-transform: rotate(120deg);
  transform: rotate(120deg);
}

.spinner-10 .sk-circle-10-6 {
  -webkit-transform: rotate(150deg);
  -ms-transform: rotate(150deg);
  transform: rotate(150deg);
}

.spinner-10 .sk-circle-10-7 {
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);
}

.spinner-10 .sk-circle-10-8 {
  -webkit-transform: rotate(210deg);
  -ms-transform: rotate(210deg);
  transform: rotate(210deg);
}

.spinner-10 .sk-circle-10-9 {
  -webkit-transform: rotate(240deg);
  -ms-transform: rotate(240deg);
  transform: rotate(240deg);
}

.spinner-10 .sk-circle-10-10 {
  -webkit-transform: rotate(270deg);
  -ms-transform: rotate(270deg);
  transform: rotate(270deg);
}

.spinner-10 .sk-circle-10-11 {
  -webkit-transform: rotate(300deg);
  -ms-transform: rotate(300deg);
  transform: rotate(300deg);
}

.spinner-10 .sk-circle-10-12 {
  -webkit-transform: rotate(330deg);
  -ms-transform: rotate(330deg);
  transform: rotate(330deg);
}

.spinner-10 .sk-circle-10-2:before {
  -webkit-animation-delay: -1.1s;
  animation-delay: -1.1s;
}

.spinner-10 .sk-circle-10-3:before {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}

.spinner-10 .sk-circle-10-4:before {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}

.spinner-10 .sk-circle-10-5:before {
  -webkit-animation-delay: -0.8s;
  animation-delay: -0.8s;
}

.spinner-10 .sk-circle-10-6:before {
  -webkit-animation-delay: -0.7s;
  animation-delay: -0.7s;
}

.spinner-10 .sk-circle-10-7:before {
  -webkit-animation-delay: -0.6s;
  animation-delay: -0.6s;
}

.spinner-10 .sk-circle-10-8:before {
  -webkit-animation-delay: -0.5s;
  animation-delay: -0.5s;
}

.spinner-10 .sk-circle-10-9:before {
  -webkit-animation-delay: -0.4s;
  animation-delay: -0.4s;
}

.spinner-10 .sk-circle-10-10:before {
  -webkit-animation-delay: -0.3s;
  animation-delay: -0.3s;
}

.spinner-10 .sk-circle-10-11:before {
  -webkit-animation-delay: -0.2s;
  animation-delay: -0.2s;
}

.spinner-10 .sk-circle-10-12:before {
  -webkit-animation-delay: -0.1s;
  animation-delay: -0.1s;
}

@-webkit-keyframes spinner-10-circle-fade-delay {
  0%, 39%, 100% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
}

@keyframes spinner-10-circle-fade-delay {
  0%, 39%, 100% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
}

/* color themes */

.spinner-10-default:before {
  background-color: var(--bmx-default);
}

.spinner-10-primary:before {
  background-color: var(--bmx-primary);
}

.spinner-10-secondary:before {
  background-color: var(--bmx-secondary);
}

.spinner-10-success:before {
  background-color: var(--bmx-success);
}

.spinner-10-warning:before {
  background-color: var(--bmx-warning);
}

.spinner-10-info:before {
  background-color: var(--bmx-info);
}

.spinner-10-danger:before {
  background-color: var(--bmx-danger);
}
